@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

h1 {
  font-size: 48px;
}

a {
  font-size: 24px;
}

.home__container {
  display: flex;
  height: 100vh;
}

.connect {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

.connect a {
  text-decoration: none;
  color: #74798C;
  font-size: 20px;
}

.horizontal__nav {
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 91px;
  padding: 0 30px;
}

.horizontal__nav .logo {
  height: 50px;
  margin-left: 5px;
}

.horizontal__nav .nav-link {
  text-decoration: none;
  color: white;
  margin: 0 20px;
  padding: 20px 0;
  font-size: 20px;
}

.horizontal__nav .navlink.active {
  text-decoration: none;
  color: white;
}

.vertical__nav {
  position: relative;
  width: 117px;
  background-color: black;
  display: flex;
  min-height: 780px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.vertical__nav .nav-icon {
  height: 60px;
  width: 60px;
  margin: 5px 0;
}

.vertical__nav .copyright {
  color: white;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  width: -webkit-max-content;
  width: max-content;
  font-size: 12px;
  position: absolute;
  bottom: 80px;
}

.dashboard__main {
  display: grid;
  grid-template-columns: 70px 1fr;
}

.dashboard__main .dashboard__container {
  padding: 3% 0% 1% 6%;
  right: 0;
  bottom: 0;
  left: 50px;
  top: 50px;
}

.dashboard__main .dashboard__container .welcome {
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 5px;
}

.dashboard__main .dashboard__container .welcome .user__name {
  color: #FF0101;
}

.dashboard__main .dashboard__container .dashboard__charts {
  margin-top: 50px;
  display: grid;
      grid-template-areas: "a a a b" "c d e b";
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 3% 2%;
  gap: 3% 2%;
  height: 50%;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__bargraph {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: a;
  height: 300px;
  background-color: #FBFBFB;
  padding: 5px;
  border-radius: 5px;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__bargraph .chart__bargraph__legend {
  display: flex;
  justify-content: space-between;
  margin: 20px 20px;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__bargraph .chart__bargraph__legend--title {
  font-weight: bold;
}

