  color: #74798C;
  font-weight: bold;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__bargraph .chart__bargraph__legend__values--ul li:first-child::marker {
  color: #282D30;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__bargraph .chart__bargraph__legend__values--ul li:last-child::marker {
  color: #E60000;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__bargraph .bargraph__chart__tooltip {
