  width: 39px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__linegraph .label {
  font-size: 8px;
}

.dashboard__main .dashboard__container .dashboard__charts .chart__radargraph {
  margin-top: 15px;
  width: 230px;
  height: 230px;
  -ms-grid-row: 2;
  -ms-grid-column: 2;
  grid-area: d;
  background-color: #282d30;
  border-radius: 5px;
}

